import * as React from "react"
import type { HeadFC, PageProps } from "gatsby"

// Importing the Bootstrap CSS
import 'bootstrap/dist/css/bootstrap.min.css';

import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { Button, Tab, Tabs } from "react-bootstrap";

import type { components } from '../sdk'

const borderColor = 'rgb(224, 229, 229)';
const backgroundColor = 'white';

const styles: { [key: string]: React.CSSProperties } = {
  page: {
    display: 'flex',
    position: 'absolute',
    top: 0,
    left: 0,
    flexDirection: 'column',
    height: '100vh',
    width: '100vw',
    margin: 0,
    padding: 0,
  },
  header: {
    paddingLeft: 12,
    paddingRight: 12,
    background: 'white',
    fontWeight: 'bold',
  },
  content: {
    borderTop: `solid 1px ${borderColor}`,
    display: 'flex',
    color: "#232129",
    flexDirection: 'row',
    flexGrow: 2,
    margin: 0,
    padding: 0,
  },
  sidebar: {
    display: 'flex',
    flexDirection: 'column',
    width: '20%',
    minWidth: 250,
    maxWidth: 500,
    height: '100%',
    background: backgroundColor,
    borderRight: `solid 1px ${borderColor}`,
  },
  sidebarItem: {
    display: 'flex',
    width: '100%',
    padding: 12,
    borderTop: 'none',
    borderLeft: 'none',
    borderRight: 'none',
    borderBottom: `solid 1px ${borderColor}`,
    borderRadius: 0,
    background: backgroundColor,
    color: 'inherit',
  },
}

/*
  * Rooms
    * Switches
      *  Configuration

  Sidebar for rooms and switches
  Main window for configuration?
 */

const IndexPage: React.FC<PageProps> = () => {
  let [loading, setLoading] = React.useState(false)
  let [config, setConfig] = React.useState<components["schemas"]["Config"] | undefined>(undefined)
  let [currentSwitch, setCurrentSwitch] = React.useState<string | undefined>(undefined)
  let [currentLayout, setCurrentLayout] = React.useState<string | undefined>(undefined)

  React.useEffect(() => {
    if (loading) {
      return () => {}
    }

    let ignore = false
    setLoading(true)

    fetch('./api/config')
      .then(res => {
        res.json().then(json => {
          const configJSON = json as components["schemas"]["Config"]
          const switches = configJSON.switches

          setConfig(configJSON)
          setCurrentSwitch(Object.keys(switches).length > 0
            ? Object.keys(switches)[0]
            : undefined)
        })
      })

    return () => {
      ignore = true
    }
  }, [loading, setLoading, config, setConfig])

  let remotes = Object.keys(config?.switches ?? {}).map(name => (
    <Button
      key={name}
      style={styles.sidebarItem}
      onClick={() => {
        console.log(`Selecting ${name}`, config?.switches[name])
        setCurrentSwitch(name)
        setCurrentLayout(Object.keys(config?.switches[name].layouts ?? {})[0])
      }}
    >
      {name}
    </Button>
  ))

  return (
    <main style={styles.page}>
      <Navbar expand="lg" style={styles.header}>
          <Navbar.Brand href="#home">Smart Switches</Navbar.Brand>
      </Navbar>
      <div style={styles.content}>
        <div style={styles.sidebar}>
          {remotes}
        </div>
        <div style={styles.mainContent}>
          {currentSwitch 
            ? <Tabs
                onSelect={(eventKey) => {
                  if (eventKey == "add-layout") {
                    console.log('Adding new layout')
                    return
                  }

                  if (eventKey) {
                    setCurrentLayout(eventKey)
                  }
                }}
              >
                {Object
                  .keys(config?.switches[currentSwitch].layouts ?? {})
                  .map(layoutName => (
                    <Tab eventKey={layoutName} title={layoutName}>Editing {layoutName}</Tab>
                  ))}
                <Tab eventKey="add-layout" title="Add layout..."></Tab>
              </Tabs>
            : "Select a switch"}
        </div>
      </div>
    </main>
  )
}

export default IndexPage
